@font-face {
  font-family: 'lato-black';
  src: url("fonts/lato/lato-black-webfont.woff"), url("fonts/lato/lato-black-webfont.woff2");
}
@font-face {
  font-family: 'lato-bold';
  src: url("fonts/lato/lato-bold-webfont.woff"), url("fonts/lato/lato-bold-webfont.woff2");
}
@font-face {
  font-family: 'lato-regular';
  src: url("fonts/lato/lato-regular-webfont.woff"), url("fonts/lato/lato-regular-webfont.woff2");
}
@font-face {
  font-family: 'merriweather-bolditalic';
  src: url("fonts/merriweather/merriweather-bolditalic-webfont.woff"), url("fonts/merriweather/merriweather-bolditalic-webfont.woff2");
}
@font-face {
  font-family: 'merriweather-bold';
  src: url("fonts/merriweather/merriweather-bold-webfont.woff"), url("fonts/merriweather/merriweather-bold-webfont.woff2");
}
@font-face {
  font-family: 'merriweather-regular';
  src: url("fonts/merriweather/merriweather-regular-webfont.woff"), url("fonts/merriweather/merriweather-regular-webfont.woff2");
}
@font-face {
  font-family: 'montserrat-bold';
  src: url("fonts/montserrat/montserrat-bold-webfont.woff"), url("fonts/montserrat/montserrat-bold-webfont.woff2");
}
@font-face {
  font-family: 'futuraptbold';
  src: url("fonts/futuraptbold.woff"), url("fonts/futuraptbold.woff2");
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  line-height: 1;
  background-color: #fff;
  text-rendering: optimizeLegibility;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
}
:focus {
  outline: none;
}
p,
dd,
dl,
figure,
blockquote {
  margin: 0;
}
blockquote,
q {
  quotes: none;
}
ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}
audio,
video {
  display: block;
}
img {
  display: inline-block;
  border: none;
}
iframe {
  border: none;
}
pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: inherit;
}
a {
  background-color: transparent;
  text-decoration: none;
  color: inherit;
}
abbr {
  border: none;
  text-decoration: none;
}
b,
strong {
  font-weight: inherit;
}
i,
em {
  font-style: inherit;
}
dfn {
  font-style: inherit;
}
mark {
  background-color: transparent;
  color: inherit;
}
small {
  font-size: inherit;
}
button,
input,
optgroup,
select,
textarea {
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 0;
  box-shadow: none;
  background-color: transparent;
  font: inherit;
  color: inherit;
  letter-spacing: inherit;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-align: left;
  text-transform: none;
}
button,
[type='button'],
[type='reset'],
[type='submit'] {
  cursor: pointer;
  appearance: none;
}
textarea {
  resize: none;
  overflow-y: auto;
  overflow-x: hidden;
}
button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
  border: none;
  padding: 0;
}
button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
  outline: none;
}
[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
  height: auto;
}
[type='search']::-webkit-search-decoration {
  appearance: none;
}
[type='search'] {
  outline: none;
}
fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
legend {
  display: block;
  padding: 0;
  white-space: normal;
}
select {
  appearance: none;
}
select::-ms-expand {
  display: none;
}
::-webkit-input-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
::-moz-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
:-moz-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
:-ms-input-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
:focus::-webkit-input-placeholder {
  opacity: 0;
}
:focus::-moz-placeholder {
  opacity: 0;
}
:focus:-moz-placeholder {
  opacity: 0;
}
:focus:-ms-input-placeholder {
  opacity: 0;
}
svg {
  display: block;
  width: 100%;
  height: 100%;
}
[hidden] {
  display: none;
}
:disabled,
.disabled {
  cursor: not-allowed;
}
::-ms-clear {
  display: none;
}
::selection {
  color: #fff;
  background-color: inherit;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page {
  font: 14px/24px 'lato-ragular', sans-serif, arial;
  color: #a7a7a7;
}
.sect {
  position: relative;
}
.container {
  max-width: 1172px;
  margin: 0 auto;
}
.sect__title {
  font: 100px/1 'montserrat-bold', sans-serif, arial;
  text-transform: uppercase;
}
.sect__title + p {
  font: 33px/1 'merriweather-bold', sans-serif, arial;
}
.nb {
  font: 14px/24px 'montserrat-bold', sans-serif, arial;
}
.visually-hidden {
  position: absolute;
  z-index: -$z-index1;
  width: 0;
  height: 0;
  margin: 0;
  border: none;
  overflow: hidden;
}
.intro__list {
  display: flex;
  justify-content: space-between;
  padding: 150px 0;
}
.intro__item {
  padding: 0 30px;
  flex: 1 0 270px;
  text-align: center;
}
.intro__item:not(:last-child) {
  margin-right: 30px;
}
.intro__item h3 {
  font: 15px/30px 'montserrat-bold', sans-serif, arial;
  text-transform: uppercase;
  color: #202121;
}
.intro__pic {
  margin-bottom: 24px;
}
.brand {
  padding: 150px 0;
}
.brand__figure {
  margin-bottom: 110px;
  text-align: center;
}
.brand__rows {
  margin-bottom: 100px;
}
.brand__rows > .brand__row {
  display: flex;
  justify-content: center;
}
.brand__rows > .brand__row:not(:last-child) {
  margin-bottom: 30px;
}
.brand__rows > .brand__row > .brand__col {
  flex: 1 0 271px;
}
.brand__rows > .brand__row > .brand__col:not(:last-child) {
  margin-right: 30px;
}
.brand__col {
  flex: 1 0 271px;
  padding-top: 160px;
}
.brand__col:not(:last-child) {
  margin-right: 30px;
}
.brand__brand {
  position: relative;
  z-index: 9;
}
.brand__link,
.brand__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.brand__link {
  z-index: 999;
  display: block;
  background-color: #f3f3f4;
  border: 1px solid #f3f3f4;
}
.brand__img {
  z-index: 99;
  object-fit: none;
}
.brand__link:hover {
  background-color: #fff;
  border: 1px solid #202121;
}
.news__inner {
  padding: 152px 0;
}
.news__header {
  max-width: 680px;
  margin: 0 auto;
  padding-bottom: 100px;
  text-align: center;
}
.news__header img {
  margin: 40px 0;
}
.news__title {
  color: #202121;
}
.news__title + p {
  color: #202121;
}
.news__rows > .news__row {
  display: flex;
  justify-content: center;
}
.news__rows > .news__row:not(:last-child) {
  margin-bottom: 72px;
}
.news__rows > .news__row > .news__col {
  flex: 1 0 271px;
}
.news__rows > .news__row > .news__col:not(:last-child) {
  margin-right: 30px;
}
.news__news {
  text-align: center;
  cursor: pointer;
}
.news__news span {
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
}
.news__news h3 {
  margin-bottom: 12px;
  font: 18px/31px 'merriweather-bold', sans-serif, arial;
  color: #202121;
}
.news__pic {
  position: relative;
  z-index: 9;
  margin-bottom: 28px;
  padding-top: 300px;
}
.news__pic > img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.news__hover {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  display: none;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  background-color: rgba(146,205,207,0.9);
}
.news__hover::after {
  position: absolute;
  top: 2.5%;
  left: 2.5%;
  z-index: -1;
  width: 95%;
  height: 95%;
  border: 5px solid #fff;
  content: '';
}
.news__hover a {
  font: 14px/32px 'montserrat-bold', sans-serif, arial;
  text-transform: uppercase;
  color: rgba(255,255,255,0.9);
}
.news__hover a:hover {
  color: #fff;
}
.news__cont {
  padding: 0 36px;
}
.news__news:hover .news__hover {
  display: flex;
}
.news__news:hover .news__hover::after {
  display: block;
}
.news__news:hover h3 {
  color: #92cdcf;
}
.sign__inner {
  padding: 150px 0;
}
.sign__title {
  font: 32px/1 'merriweather-bold', sans-serif, arial;
  text-transform: inherit;
  text-align: center;
  color: #202121;
}
.sign__form {
  max-width: 430px;
  margin: 100px auto;
  text-align: center;
}
.sign__input {
  width: 100%;
  padding: 30px;
  font: 14px/30px 'montserrat-bold', sans-serif, arial;
  text-align: center;
  color: #202121;
  border-bottom: 1px solid #202121;
}
.sign__input::-webkit-input-placeholder {
  text-transform: uppercase;
}
.sign__input::-moz-placeholder {
  text-transform: uppercase;
}
.sign__input:-moz-placeholder {
  text-transform: uppercase;
}
.sign__input:-ms-input-placeholder {
  text-transform: uppercase;
}
.caro {
  max-width: 970px;
  margin: 0 auto;
  overflow: hidden;
}
.caro__wrap {
  display: flex;
}
.caro__slide {
  flex: 1 0 100%;
  text-align: center;
}
.caro__slide p {
  font: 30px/50px 'merriweather-bolditalic', sans-serif, arial;
  color: #a7a7a7;
}
.caro__slide span {
  text-transform: uppercase;
  color: #202121;
}
.caro__quot {
  width: 70px;
  height: 70px;
  margin: 0 auto;
  line-height: 75px;
  background-color: #92cdcf;
  border-radius: 50%;
}
.caro__quot-img {
  object-fit: none;
}
.caro__text {
  margin: 70px 0 60px 0;
}
.caro__nav {
  padding-top: 70px;
  text-align: center;
}
.caro__nav > li {
  display: inline-block;
  width: 8px;
  height: 8px;
  vertical-align: middle;
}
.caro__nav > li:not(:last-child) {
  margin-right: 12px;
}
.caro__dot {
  display: block;
  width: 6px;
  height: 6px;
  margin: 0 auto;
  background-color: #a7a7a7;
  border-radius: 50%;
}
.caro__dot.active {
  width: 100%;
  height: 100%;
  background-color: #202121;
}
.social {
  text-align: center;
}
.social__item {
  display: inline-block;
}
.social__item:not(:last-child) {
  margin-right: 53px;
}
.social__link {
  font: 10px/1 'montserrat-bold', sans-serif, arial;
  text-transform: uppercase;
  color: rgba(146,205,207,0.7);
}
.social__link:hover {
  color: #92cdcf;
}
